@import './app/variables/variables.scss';

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: $basic-font;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}

.container {
  max-width: $big-screen;
  margin: 0 auto;
}

.body {
  margin: 0;
  padding: 0;
  background-color: #fff;
}

#root {
  width: 100%;
  height: 100%;
	min-height: 100vh;
	position: relative;
}

//for disable text selection
.unselectable {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none;   /* Chrome/Safari/Opera */
  -khtml-user-select: none;    /* Konqueror */
  -moz-user-select: none;      /* Firefox */
  -ms-user-select: none;       /* Internet Explorer/Edge */
  user-select: none;           /* Non-prefixed version, currently not supported by any browser */
}

/* width */
::-webkit-scrollbar {
  width: var(--custom-scroll-width);
}

/* Track */
::-webkit-scrollbar-track {
  background: var(--custom-scroll-track-color);
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: var(--custom-scroll-thumb-color);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: var(--custom-scroll-thumb-hover-color);
}
