@import '../../../../variables/variables.scss';

.weather-app-location-page {
	width: 100%;
	height: 100%;
	min-height: 100vh;
	background-color: var(--weather-page-bg-color);
	padding: 0 20px;

	display: flex;
	justify-content: center;
	align-items: center;
}

.weather-app-location-page__main {
	width: 100%;
	height: 100%;

	display: flex;
	justify-content: center;
	align-items: center;
}

.weather-app-location-page__application  {
	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-template-rows: 1fr;

	background-color: var(--weather-application-bg-color);
	border-radius: var(--weather-basic-border-radius);
	box-shadow: var(--weather-application-box-shadow-color);

	@media (max-width: $mobile-width) {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		gap: 20px;
	}
}
